
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  max-width: 550px;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
nav ul li {
margin:0 auto;
  margin-right: 1em;
  border-radius: 70%;
  border: 2px;
  padding-top: 20px;
  padding-bottom:20px;
}
.title{
margin:0 auto;
border-radius: 25px;
   background: #73AD21;
   padding: 12px;
   padding-left:20px;
   padding-right:20px;
   width: 200px;
   height: 40px;
}


